html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

nav,
ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

a,
span,
*:before,
*:after {
  display: inline-flex;
  text-decoration: none;
  color: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 5.55em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  color: #fff;
}

.container {
  max-width: 768px;
  width: 100%;
  margin: 0 auto;
  display: flex;
}

@media only screen and (max-width: 550px) {
  .container {
    display: block;
    text-align: center;
  }
}

.contact-heading {
  font-weight: 600;
  margin-bottom: 7px;
}

.contact-heading_footer {
  margin-bottom: 16px;
}

.text {
  font-weight: 600;
}

.contact-tel_footer {
  margin-bottom: 14px;
  font-size: 14px;
}

.communications_footer {
  margin-top: 14px;
  line-height: 1.3;
}

.address_footer {
  line-height: 1.3;
}

.info-corporate_footer {
  position: relative;
}

.info-corporate_footer:before {
  position: absolute;
  left: calc(100% + 27px);
  content: '';
  height: 100%;
  width: 1px;
  background: #929499;
}

.header-main {
  padding: 28px 60px;
  display: flex;
  max-width: 1120px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  flex-wrap: wrap;
}

.nav-wrapper {
  margin-left: auto;
  position: relative;
  bottom: 15px;
}

.nav-main {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

@media only screen and (max-width: 550px) {
  .nav-main {
    justify-content: center;
  }
}

.nav-main li {
  text-transform: uppercase;
  margin-right: 22px;
  font-size: 12px;
  position: relative;
  display: flex;
  justify-content: center;
  padding: 8px 0px;
}

.nav-main li:hover:before {
  width: 100%;
}

.nav-main li:before {
  content: '';
  position: absolute;
  bottom: 0;
  width: 0;
  height: 3px;
  background: #2effc0;
  transition: all .3s ease;
}

.baner {
  display: flex;
  align-items: center;
  min-height: 227px;
  padding-top: 245px;
  padding-bottom: 160px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 0;
  box-shadow: 1px 10px 8px rgba(0, 0, 0, 0.07);
}

.baner__img {
  max-width: 20%;
}

.baner__heading {
  color: #2effc0;
  font-size: 41px;
  font-size: 30px;
}

.lang-switcher {
  font-size: 12px;
  color: #fff;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 12px;
}

.lang-switcher__item {
  margin-left: 12px;
  position: relative;
}

.lang-switcher__item:not(:last-child):before {
  content: "/";
  position: absolute;
  left: calc(100% + 6.5px);
}

.btn {
  border: none;
  border-radius: 50px;
  text-transform: uppercase;
  font-weight: 600;
  transition: all 1s ease;
}

.btn_primary {
  background: #2effc0;
  font-size: 12px;
  padding: 7px 18px;
  color: #001029;
}

.btn_primary:hover {
  background: #001029;
  color: #2effc0;
}

.btn_submit {
  font-size: 15px;
  color: #d2d5d9;
  background: #001029;
  padding: 6px 50px;
  justify-content: center;
  display: flex;
  margin: 0 auto;
  line-height: 2;
}

.btn_submit:hover {
  background: #2effc0;
}

.user-form {
  position: relative;
  bottom: 70px;
  background: #fff;
  border: 1px solid #fbfbfb;
  max-width: 260px;
  width: 100%;
  padding: 22px 27px 37px;
  border-radius: 16px;
  box-shadow: 1px 10px 33px rgba(0, 0, 0, 0.07);
  margin-right: 65px;
}

@media only screen and (max-width: 550px) {
  .user-form {
    margin: 0 auto;
  }
}

.user-form__area {
  color: #001029;
}

.user-form__area label {
  font-size: 12px;
  line-height: 2.79;
  font-weight: 600;
}

.user-form__area input {
  border: 1px solid #dedede;
  border-radius: 5px;
  padding: 7px;
  border-radius: 4px;
  box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.07);
  line-height: 1.45;
  outline: none;
  transition: .18s ease-out;
  width: 100%;
}

.user-form__area input:hover {
  box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.02);
}

.user-form__area input:focus {
  color: #4b515d;
  border: 1px solid #B8B6B6;
  box-shadow: inset 1px 2px 4px rgba(0, 0, 0, 0.01), 0px 0px 8px rgba(0, 0, 0, 0.2);
}

.user-form__description {
  font-size: 16px;
  color: #00112a;
  line-height: 1.25;
}

.user-form p {
  margin-bottom: 25px;
}

select {
  width: 100%;
  border: 1px solid #dedede;
  border-radius: 4px;
  box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.07);
  margin-bottom: 40px;
}

select:focus, select:hover {
  background: #eeeeee;
}

select option {
  font-size: 16px;
  padding: 7px;
  text-align: center;
}

.user-contact-us__more {
  font-size: 15px;
  color: #001029;
  line-height: 1.2;
  padding-top: 50px;
}

.contact-info {
  width: 185px;
}

@media only screen and (max-width: 550px) {
  .contact-info {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.contact-info > * {
  margin-bottom: 35px;
}

@media only screen and (max-width: 550px) {
  .contact-info > * {
    flex: 1 1 33%;
  }
}

@media only screen and (max-width: 550px) {
  .contact-info > :nth-child(odd) {
    margin-right: 5%;
  }
}

.contact-info__sales {
  margin-bottom: 38px;
}

.footer {
  font-size: 16px;
  background: #001029;
}

.footer__wrap {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 65px;
  padding: 70px 0px;
}

@media only screen and (max-width: 820px) {
  .footer__wrap {
    grid-template-columns: repeat(2, 1fr);
    margin: 0 auto;
    padding: 70px 15px;
  }
}

.copyright {
  background: #001029;
  display: flex;
  justify-content: center;
  padding-bottom: 22px;
  font-size: 11px;
  line-height: 2.45;
  color: #fff;
}
